export default {
	data() {
		return {
			image: ''
		}
	},
	onLoad() {
		this.getData()
	},
	methods: {
		async getData() {
			uni.showLoading({
				title: "载入中",
				mask:true
			})
			const res = await this.$http({
				url:'/in_theaters'
				})
			this.image = res[1].data.subjects[0].images.small
			uni.hideLoading()
		},
		enter(e) {
			uni.redirectTo({
				url:'../home/home'
			})
		}
	},
}
